<template>
    <svg version="1.1" class="location" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px" y="0px"
	 width="160px" height="80px" viewBox="0 0 200 100" enable-background="new 0 0 200 100" xml:space="preserve">
        <rect @click="clickleft" fill="grey" width="100" height="100"/>
        <rect @click="clickright" x="100" fill="grey" width="100" height="100"/>
        <g>
            
                <line fill="none" stroke="#000000" stroke-width="0.5" stroke-miterlimit="10" x1="142.438" y1="46.183" x2="156.427" y2="46.183"/>
            
                <line fill="none" stroke="#000000" stroke-width="0.5" stroke-miterlimit="10" x1="142.438" y1="49.864" x2="156.427" y2="49.864"/>
            
                <line fill="none" stroke="#000000" stroke-width="0.5" stroke-miterlimit="10" x1="142.438" y1="53.546" x2="156.427" y2="53.546"/>
        </g>
        <g>
            <line fill="none" stroke="#000000" stroke-width="0.5" stroke-miterlimit="10" x1="51.375" y1="37.983" x2="51.375" y2="58.6"/>
            <circle cx="51.375" cy="58.6" r="4.418"/>
        </g>
    </svg>
</template>

<script type="text/ecmascript-6">
export default {
    props: {

    },
    data() {
        return {
            routerflag: true
        };
    },
    computed: {

    },
    created() {

    },
    mounted() {

    },
    watch: {

    },
    methods: {
        clickleft() {
            if(this.routerflag){
            this.$router.push('address')
            this.routerflag = !this.routerflag
            } else {
                this.$router.go(-1)
                this.routerflag = !this.routerflag
            }
        },
        clickright() {
            if(this.routerflag){
            this.$router.push('productList')
            this.routerflag = !this.routerflag
            } else {
                this.$router.go(-1)
                this.routerflag = !this.routerflag
            }
        }
    },
    components: {

    },
};
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
    .location
        position: fixed
</style>



